import React from 'react';

import burgerLogo from '../../assets/images/png.png';
import classes from './Logo.css';

const logo = (props) => (
    <div className={classes.Logo} style={{height: props.height}}>
        <img src={burgerLogo} alt="Whad'a-Paav"/>
    </div>

);

export default logo;


